module pacman {
  'use strict';

  /**
   * Constants to use for keys in our assets.  We cannot use an enum since
   * TypeScript does not yet support enums of strings.
   */
  export class Sounds {
    static CHASING_GHOSTS: string = 'chasingGhosts';
    static CHOMP_1: string = 'chomp1';
    static CHOMP_2: string = 'chomp2';
    static DIES: string = 'dies';
    static EATING_FRUIT: string = 'eatingFruit';
    static EATING_GHOST: string = 'eatingGhost';
    static EXTRA_LIFE: string = 'extraLife';
    static INTERMISSION: string = 'intermission';
    static EYES_RUNNING: string = 'eyesRunning';
    static OPENING: string = 'opening';
    static SIREN: string = 'siren';
    static TOKEN: string = 'token';
  }
}
